import React from 'react';
import { Link } from 'react-router-dom';
import "../css/LinkStyle.css"
// Bootstrap
import { Navbar, Container, Row, Col, Card, Carousel} from 'react-bootstrap';
import "../css/Carousel.css"


class CardSliderCol extends React.Component {
	render() {
		return (
			<Col md={2}>
	  			<figure className="figure">
                    <img src="https://tupian.tupianzy.com/pic/upload/vod/2019-01-16/201901161547644844.jpg" className="figure-img img-fluid rounded" alt="..." />
                    <figcaption className="figure-caption text-right">Spider Man</figcaption>
                </figure>
	  		</Col>
		);
	}
}

class CardSliderRow extends React.Component {
	render() {
		return (
			<Row>
		  		<CardSliderCol />
		  		<CardSliderCol />
		  		<CardSliderCol />
		  		<CardSliderCol />
				<CardSliderCol />
		  		<CardSliderCol />
		  	</Row>
		);
	}
}

class CardSlider extends React.Component {
	render() {
		return (
			<Carousel indicators={false} interval={999999}>
			  <Carousel.Item>
			  	<CardSliderRow />
			  </Carousel.Item>
			  <Carousel.Item>
			  	<CardSliderRow />
			  </Carousel.Item>
			</Carousel>
		);
	}
}

class RecMovieCard extends React.Component {
	render() {
		return (
			<Card>
			  <Card.Header className="shadow-lg bg-light">
			  	<Navbar.Brand as={Link} to={"/Movie-Website/" + this.props.series}>
			  		<h2 className="text-dark py-2 pr-4 m-0 display-4 font-weight-bold">
				  		{this.props.series + " Movies"}
              		</h2>
                </Navbar.Brand>
              </Card.Header>
			  <Card.Body>
			  	<CardSlider />
			  </Card.Body>
			</Card>
		);
	}
}

