class GenrePage extends React.Component {
	constructor(props){
		super(props);
		this.state = {
					    currentMovies: [],
					    currentPage: null,
					    totalPages: null,
					    count: -1,
					  };
		console.log("I am here: " + this.state.count);	
		this.onPageChanged = this.onPageChanged.bind(this);
	}
	
	componentDidUpdate(prevProps) {
		const type = this.props.match.params.type;
		const prevType = prevProps.match.params.type
		if (type !== prevType) {
			console.log()
			getMoviesFromMongoDB("find_from_to", {"genre": type, "start": 0, "end": 24})
			.then(response => {
				const totalPages = Math.ceil(response.count / this.props.pageLimit);
				this.setState({count: response.count, totalPages: totalPages, currentPage: 1, currentMovies: JSON.parse(JSON.parse(response.movie))})
			});
		}
	}

	componentDidMount() {
		const { match: { params } } = this.props;
    	const { type } = params;
    	getMoviesFromMongoDB("find_from_to", {"genre": type, "start": 0, "end": 1})
			.then(response => {this.setState({count: response.count});});
	}

	onPageChanged = data => {
		const { currentPage, totalPages, pageLimit } = data;
		const offset = (currentPage - 1) * pageLimit;
		const {type} = this.props.match.params;

		getMoviesFromMongoDB("find_from_to", {"genre": type, "start": offset, "end": 24})
			.then(response => JSON.parse(JSON.parse(response.movie)))
			.then(data => {this.setState({currentMovies: data});});

		this.setState({ currentPage, totalPages });
	};

  render() {

    console.log("count: " + this.state.count);

    if (this.state.count === -1) return null;
    const headerClass = [
      "text-dark py-2 pr-4 m-0",
      this.state.currentPage ? "border-gray border-right" : ""
    ]
      .join(" ")
      .trim();

    return (
      <div className="container mb-7">
        <div className="row d-flex flex-row py-5">
          <div className="w-100 px-4 py-5 d-flex flex-row flex-wrap align-items-center justify-content-between">
            <div className="d-flex flex-row align-items-center">
              <h2 className={headerClass}>
                <strong className="text-info">{this.state.count}</strong>{" "}
                <strong className="text-secondary">Movies</strong>
              </h2>
              {this.state.currentPage && (
                <span className="current-page d-inline-block h-100 pl-4 text-secondary">
                  Page <span className="font-weight-bold text-info">{this.state.currentPage}</span> /{" "}
                  <span className="font-weight-bold">{this.state.totalPages}</span>
                </span>
              )}
            </div>
            <div className="d-flex flex-row py-4 align-items-center">
              <Pagination
                totalRecords={this.state.count}
                pageLimit={this.props.pageLimit}
                pageNeighbours={5}
                onPageChanged={this.onPageChanged}
              />
            </div>
            <Container>
	        	<Card>
				  <Card.Header className="shadow-lg bg-light">
				  	<Navbar.Brand>
				  	<h2 className="text-dark py-2 pr-4 m-0 display-4 font-weight-bold">
				  		{this.props.match.params.type + " Movies"}
              		</h2>
	                </Navbar.Brand>
	              </Card.Header>
				  <Card.Body>
				  	<Row>
				  	{this.state.currentMovies.map((item, i) => 
				  		<Col key={"movie-row"+i} style={{marginTop:10}} xs={6} md={3}>
				        	<Card className="shadow-lg bg-light">
								  <Image  
								    onError={(e)=>{e.target.onerror = null; e.target.src="https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg"}}
								    variant="top" 
								    style={{width: 350, height: 262}} 
								    src={item.Poster}  fluid/>
								  <Card.Body>
								  	<Card.Text>
									    {item.Title}
									</Card.Text>
								  </Card.Body>
								</Card>
				        </Col>
				  	)}
    				</Row>
				  </Card.Body>
				</Card>
		    </Container>
          </div>
        </div>
      </div>
    );
  }
}


const Page1 = () => (
    <div>
        <h1>Page1</h1>
    </div>
)

function getMoviesFromMongoDB(type, context) {
	var url = "https://gdjactscaf.execute-api.us-east-1.amazonaws.com/Prod/retrieveMovies?";
	const title = context.title;
	const genre = context.genre;
	const start = context.start;
	const end = context.end;

	if (type === "find_one") {
		url = url + "type=" + type + "&title=" + title;
	}
	else {
		url = url + "type=" + type + "&genre=" + genre + "&start=" + start + "&end=" + end;
	}

	

	return fetch(url)
	      .then(res => res.json())
	      .then(
	        (result) => {
	        	// console.log(result);
	          return result;
	        },
	        // Note: it's important to handle errors here
	        // instead of a catch() block so that we don't swallow
	        // exceptions from actual bugs in components.
	        (error) => {
	          alert(error);
	        }
	       );
	
}


export default Home



export {GenrePage, Page1}

